body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.uk-navbar-item, .uk-navbar-nav>li>a, .uk-navbar-toggle {
  min-height: 70px !important;
}

textarea {
  resize: none ;
}

@media (max-width: 639px) {
  .uk-navbar-item, .uk-navbar-nav>li>a, .uk-navbar-toggle {
    min-height: 60px !important;
  }
}

.uk-logo, .uk-navbar-nav li a, .uk-navbar-toggle {
  color: white !important;
}

.uk-navbar-nav li a:hover, .uk-navbar-toggle:hover, .uk-navbar-nav .navbar-nav-active {
  color: black !important;
}

.uk-card .uk-card-title {
  /* color: black !important; */
}

.uk-card {
  border: 1px solid rgba(204,204,204,0.75);
}

.uk-card-body .uk-card-title {
  color: rgb(51,51,51) !important;
}

.uk-card-body a{
  color: rgb(102,102,102) !important;
}

.uk-card-body a:hover {
  color: rgb(30,135,240) !important;
  text-decoration: none;
}

.like-button svg path {
  color: #e84a43;
  fill: #e84a43;
}

